.support_box{
    width: 100%;
    height: 140px;
    background-color:white;
    border-radius: 5px;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2);
}
.support_box_con{
    cursor: pointer;
    display: flex;
    justify-content: space-between;
}
.support_con_heading{
    font-family: 'Roboto', sans-serif;
    color: black;
    margin: 15px;
    font-size: 18px;
    font-weight: bold;
}
.support_left_box{
    padding-top: 20px;
    text-align: center;
    border-radius: 5px;
    margin: 5px;
    width: 70px;
    height: 70px;
    background-color:#F0F7FF;
}
.support_num {
    margin-left: 20px;
    font-weight: bold;
    font-size:50px;
    color: #EA7A26;
}
.sup_con{
    margin: 20px;
}
.support_main{
    margin-left: 50px;
    margin-right: 30px;
}
@media (min-width: 576px) and (max-width: 767.98px){
    .support_box{
        margin-bottom: 20px;
    }
}
@media (min-width: 768px) and (max-width: 991.98px){
    .support_box{
        margin-bottom:20px ;
    }
}
@media (min-width: 992px) and (max-width: 1199.98px){
    .support_box{
        margin-top:20px;
    }
    .support_box{
        width: 80%;
    }
    .support_main{
        margin-left: 100px;
    }
}
@media (max-width: 576px) {
    .support_box{
        margin-bottom: 20px;
    }
    .support_main{
        margin-left: 10px;
        margin-right: 10px;
    }
}
