.studentdashboard{
  margin-top: 20px;
}
.studentdashboard #contain{
  margin: 0px 30px 0px 30px;
}
@media(max-width:1100px){
  .studentdashboard #contain{
    margin: 0px;
  }
}
.studentdashboard .smallbox{
  padding:  25px 0 25px 0;
  min-height: 150px;
  background:#272C49;
  box-shadow: -4px 1px 4px rgba(0, 0, 0, 0.25);
border-radius: 30px;
text-align: center;
}
.studentdashboard .bigbox{
  padding: 25px 22px 15px 22px;
  border-radius: 30px;
  min-height: 323.5px;
  background:#272C49;
  box-shadow: -4px 1px 4px rgba(0, 0, 0, 0.25);

}
.studentdashboard .midbox{
  padding:22px;
  background:#272C49;
  box-shadow: -4px 1px 4px rgba(0, 0, 0, 0.25);
border-radius: 30px;
text-align: center;
min-height: 323.5px;

}
.studentdashboard .datebox{
text-align: center;
margin: 20px 35px 0px 10px;
border-radius: 10px;
/* box-shadow: 5px 5px #EB7926; */
 box-shadow: 3px 3px 5px 6px #EB7926;
}

#objectivequestion #radiocontainer{
    display: block;
    position: relative;
    padding-left: 45px;
    /* margin-bottom: 12px; */
    cursor: pointer;
    font-size: 20px;
    font-weight: normal;
    font-family: Montserrat;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

}
#objectivequestion #radiocontainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
#objectivequestion #radiocontainer .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 1px;
  height: 25px;
  width: 25px;
    background: none;
    border: 1px solid #EA7A26;
  border-radius: 8px;
  font-size: 17px;
  padding: 0px 0px 0px 7px;
  color:#EB7926;
}
#objectivequestion #radiocontainerselected{
    display: block;
    position: relative;
    padding-left: 45px;
    /* margin-bottom: 12px; */
    cursor: pointer;
    font-size: 20px;
    font-weight: normal;
    font-family: Montserrat;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color:white;
}
#objectivequestion #radiocontainerselected input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
#objectivequestion #radiocontainerselected .checkmarkselected{
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 1px;
  height: 25px;
  width: 25px;
    background: none;
    border: 1px solid #FFFFFF;
  border-radius: 8px;
  font-size: 17px;
  padding: 0px 0px 0px 7px;
  color:white;
}
