  .navbar .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .navbar .dropdownContent {
    display: none;
    position: absolute;
    right: 0;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .navbar .dropdownContent a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .navbar .dropdownContent a:hover {background-color: #f1f1f1;}
  .navbar .dropdown:hover .dropdownContent {display: block;}
  
