.dashboard{
    margin-top: 30px;
    }
.dashboard .box{
  /* border: 1px solid lightgrey; */
  padding: 10px 12px 0px 20px;
  border-radius: 10px;
  box-shadow: 1px 2px 5px lightgrey;
  margin-bottom: 13px;
  height: 115px;
}
.dashboard .box #text1{
  font-family: Montserrat;
font-style: normal;
font-weight: 500;
font-size: 15px;
line-height: 18px;
text-transform: capitalize;
color: #1D1D1D;
font-weight: bold;
display: inline-block;
margin-top: 10px;
}
.dashboard .box .imgbg{
  display: inline-block;
  float: right;
  background: #F0F7FF;
  padding: 10px;
  border-radius: 6px;
}
.dashboard .box #num{
  margin-top: 20px;
  font-family: Montserrat;
font-style: normal;
font-weight: 600;
font-size: 40px;
line-height: 49px;
text-transform: capitalize;
background-image: linear-gradient(246.8deg, #C4C4C4 -5.42%, #293E80 -5.41%, #09AEE5 96.08%);
background-size: 100%;
background-repeat: repeat;
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
-moz-background-clip: text;
-moz-text-fill-color: transparent;
}